import fs from 'fs';

export function writeJsonFile(filePath: string, data: string) {
    return new Promise((resolve, reject) => {
        fs.writeFile(filePath, JSON.stringify(data, null, 2), { encoding: 'utf-8' }, (err) => {
            if (err) {
                reject(err);
            } else {
                resolve();
            }
        });
    });
}
